import { styled } from 'styled-components';

import { tablet, laptop, desktop } from '#theme/devices';

import Layout from '../Layout';

export const OneColumnNarrow = styled(Layout)`
  /* column width with bleed  */
  --container-width: 100vw;

  /* min gap between the content and the edge of the viewport */
  --bleed: ${({ theme }) => theme.dimension.bleed}px;

  /* text line width */
  --line-length: calc(var(--container-width) - var(--bleed) * 2);

  max-width: var(--container-width);
  box-sizing: border-box;
  padding: 0 var(--bleed);
  margin: 0 auto;

  @media ${tablet} {
    /* 6/8 cols in a fluid container */
    --container-width: calc(
      564 / ${({ theme }) => theme.dimension.pageWidth.tablet} * 100vw
    );
  }

  @media ${laptop} {
    /* 8/12 cols in a fixed container */
    --container-width: 672px;
  }
`;

export const FullBleedContainer = styled.div`
  /* maximum width that the object can attain */
  --object-width: 100vw;

  /* amount by which the object overhangs the column */
  --overhang: calc(((var(--object-width) - var(--line-length)) / 2) * -1);

  @media ${laptop} {
    --object-width: ${({ theme }) => theme.dimension.pageWidth.laptop}px;
  }

  @media ${desktop} {
    --object-width: ${({ theme }) => theme.dimension.pageWidth.desktop}px;
  }

  width: var(--object-width);
  box-sizing: border-box;
  margin-right: var(--overhang);
  margin-left: var(--overhang);
`;

const InsetContainer = styled(FullBleedContainer)`
  /* share of total line length that the object can inhabit */
  --inset-object-width: 100vw;

  /* gap between the object and the text */
  --alley: 0;

  clear: both;

  @media ${tablet} {
    --inset-object-width: 50vw;
    --alley: ${({ theme }) => theme.spacing.x5}px;
    max-width: var(--inset-object-width);
    padding-bottom: ${({ theme }) => theme.spacing.x4}px;
  }

  @media ${laptop} {
    --inset-object-width: calc(var(--object-width) / 2);
    --alley: ${({ theme }) => theme.spacing.x6}px;
  }
`;

export const InsetLeftContainer = styled(InsetContainer)`
  @media ${tablet} {
    padding-right: var(--alley);
    margin-right: 0;
    clear: right;
    float: left;
  }
`;

export const InsetRightContainer = styled(InsetContainer)`
  @media ${tablet} {
    padding-left: var(--alley);
    margin-left: 0;
    clear: left;
    float: right;
  }
`;

export const Main = styled.div``;
